<template>
	<view>
		<view>
			<u-navbar :is-back="true" back-icon-color="white" back-text="返回" :back-text-style="{ color: 'white' }"  title="编辑资料" :background="bgColor" title-color="white" title-size="40" ></u-navbar>
		</view>
		
		<view class="item" v-model="editData">
			
			<view class="avatarAll" @click="gotoAvatar()">
				<view>头像</view>
				<view id="avatarRight">
					<view class="avatar">
						<u-avatar :src="editData.avatar" mode="square" size="large"></u-avatar>			
					</view>
					<u-icon class="icon" name="arrow-right"></u-icon>
				</view>
			</view>
			
			<view class="otherAll" @click="gotoNickname()">
				<view class="name">昵称</view>
				<view class="right">
					<view>{{editData.nickName}}</view>
					<u-icon class="icon" name="arrow-right"></u-icon>
				</view>
			</view>
			
			<view class="otherAll" @click="gotoUsername()">
				<view class="name">TimeLaw账号</view>
				<view class="right">
					<view>{{editData.username}}</view>
					<u-icon class="icon" name="arrow-right"></u-icon>
				</view>
			</view>
			
			<view class="otherAll" style="border-bottom: 1rpx solid #fff;" @click="info()">
				<view class="name">关联信息</view>
				<view class="right">
					<view>{{editData.message}}</view>
					<u-icon class="icon" name="arrow-right" :style="iconStyle"></u-icon>
				</view>
			</view>
			
		</view>
		
		
	</view>
</template>

<script>
	export default{
		data(){
			return{
				token:'',
				username:'',
				loginMethod:'',
				editData:{
					avatar: '',
					nickName: '',
					username: '',
					message: ''
				},
				bgColor:{
				  "backgroundImage": "linear-gradient(45deg, #1E90FF, 	#BA55D3)"
				},
				iconStyle:''//visibility:hidden箭头显示隐藏
				
			}
		},
		onShow(){
			uni.request({
				url:'http://localhost/users/getUserinfo',
				method:"GET",
				header:{
					token: uni.getStorageSync('token')
				},
				success: (e) => {
					console.log(e);
					if(e.data.data.loginMethod == 0){
						//账号密码登录
						this.editData.avatar = e.data.data.avatar;
						this.editData.nickName = e.data.data.nickName;
						this.editData.username = e.data.data.username;
						this.openid = e.data.data.openId;
						this.loginMethod = e.data.data.loginMethod;
						if(e.data.data.nickName == null){
							this.editData.nickName = "未设置";
						}
						if(this.openid == null){
							this.editData.message = "未关联微信账号";
							
						}else{
							this.editData.message = "已关联微信账号";
							this.iconStyle = 'visibility:hidden';
						} 
					}
					if(e.data.data.loginMethod == 1){
						//微信登录
						this.editData.avatar = e.data.data.avatar;
						this.editData.nickName = e.data.data.nickName;
						this.editData.username = e.data.data.username;
						this.username = e.data.data.username;
						this.loginMethod = e.data.data.loginMethod;
						if(e.data.data.nickName == null){
							this.editData.nickName = "未设置";
						}
						if(this.username == null){
							this.editData.message = "未关联TimeLaw账号";
							this.editData.username = "未关联TimeLaw账号";
						}else{
							this.editData.message = "已关联TimeLaw账号";
							this.iconStyle = 'visibility:hidden';
						}
					}	
				},
			});
		},
		onLoad() {
			uni.request({
				url:'http://localhost/users/getUserinfo',
				method:"GET",
				header:{
					token: uni.getStorageSync('token')
				},
				success: (e) => {
					console.log(e);
					if(e.data.data.loginMethod == 0){
						//账号密码登录
						this.editData.avatar = e.data.data.avatar;
						this.editData.nickName = e.data.data.nickName;
						this.editData.username = e.data.data.username;
						this.openid = e.data.data.openId;
						this.loginMethod = e.data.data.loginMethod;
						if(e.data.data.nickName == null){
							this.editData.nickName = "未设置";
						}
						if(this.openid == null){
							this.editData.message = "未关联微信账号";
							
						}else{
							this.editData.message = "已关联微信账号";
							this.iconStyle = 'visibility:hidden';
						} 
					}
					if(e.data.data.loginMethod == 1){
						//微信登录
						this.editData.avatar = e.data.data.avatar;
						this.editData.nickName = e.data.data.nickName;
						this.editData.username = e.data.data.username;
						this.username = e.data.data.username;
						this.loginMethod = e.data.data.loginMethod;
						if(e.data.data.nickName == null){
							this.editData.nickName = "未设置";
						}
						if(this.username == null){
							this.editData.message = "未关联TimeLaw账号";
							this.editData.username = "未关联TimeLaw账号";
						}else{
							this.editData.message = "已关联TimeLaw账号";
							this.iconStyle = 'visibility:hidden';
						}
					}	
				},
			});
		},
		created() {
			// 监听从裁剪页发布的事件，获得裁剪结果
			uni.$on('uAvatarCropper', path => {
				this.editData.avatar = path;
				// 在此上传到服务端
				uni.uploadFile({
					url: 'http://localhost/upload/picture',
					header:{
						"Content-Type": "multipart/form-data",  //uniapp中不支持直接使用 new FormData() 来定义一个formdata数据，通过这样设置就可以向后台传递formdata格式的数据了
						"token": uni.getStorageSync('token')
					},
					filePath: path,
					name: 'file',
					success: (e) => {
						//获取图片信息 http://localhost/Img_UpLoad/4399f72a7a41415fbfa4ee308ff6dc1c.jpg
						var res = JSON.parse(e.data);
						console.log(res[0].url);						
						//最终回传服务器图片路径，保存数据库
						this.editData.avatar = 'http://localhost/Img_UpLoad/' + res[0].url;						
						//写数据库
						uni.request({
							url:'http://localhost/users/uploadAvatar',
							method:"POST",
							header:{
								"token": uni.getStorageSync('token')
							},
							data:{
								"avatar": this.editData.avatar
							},
							success: (e) => {							
								console.log(e);
								//缓存服务器下发token
								uni.setStorageSync('token',e.data.data.token);
								this.editData.avatar = 'http://localhost/Img_UpLoad/' + res[0].url;	
							}
						})
						
					}
				});
			})
		},
		methods:{
			gotoAvatar(){
				// 此为uView的跳转方法，详见"文档-JS"部分，也可以用uni的uni.navigateTo
				this.$u.route({
					// 关于此路径，请见下方"注意事项"
					url: 'pages/crop_picture/crop_picture',
					// 内部已设置以下默认参数值，可不传这些参数
					params: {
						// 输出图片宽度，高等于宽，单位px
						destWidth: 300,
						// 裁剪框宽度，高等于宽，单位px
						rectWidth: 300,
						// 输出的图片类型，如果'png'类型发现裁剪的图片太大，改成"jpg"即可
						fileType: 'jpg',
					}
				})
			},
			gotoNickname(){
				uni.navigateTo({
					url:'../my_editDate_nickname/my_editDate_nickname'
				});
			},
			gotoUsername(){
				if(this.editData.username == "未关联TimeLaw账号"){
					return;
				}else{
					uni.navigateTo({
						url:'../my_editDate_username/my_editDate_username?username=' + this.editData.username
					});
				}
			},
			info(){
				if(this.loginMethod == 0 && this.openid == null){
					let code = '';
					uni.login({
						provider: 'weixin',
						success: function(res) {
							console.log(res);
							code = res.code;
						},
						fail: function(res) {
							console.log(res)
						}
					});
					uni.getUserProfile({
						desc:'获取你的昵称、头像、地区及性别',
						success: (e) => {	
							uni.request({
								url:'http://localhost/users/relationWechat',
								method:"POST",
								header:{
									"token": uni.getStorageSync('token')
								},
								data:{
									"code": code
								},
								success: (e) => {
									console.log(e);
									uni.setStorageSync('token',e.data.data.token);
									uni.showToast({
										icon:"success",
										title:'关联成功'
									});
									this.editData.message = "已关联微信账号";
									this.iconStyle = 'visibility:hidden';
								}
								
							})
						},
						fail: (e) => {
							console.log(res);
							//拒绝授权
							uni.showToast({
								title: '您拒绝了授权请求',
								icon: 'error',
								duration: 1500
							});
							return;
						}
					})
					
				}
				if(this.loginMethod == 1 && this.username == null){
					uni.navigateTo({
						url:'../my_editDate_info/my_editDate_info'
					})
				}
				
			}
		}
	}
</script>

<style scoped>
	.item{
		background-color: #fff;
	}
	.avatarAll{
		height: 140rpx; 
		margin-left: 30rpx;
		border-bottom: 1rpx solid #bfbfbf;
	}
	.avatarAll view{
		float: left;
		font-size: 30rpx;
		color: #000;
		height: 140rpx; 
		line-height: 140rpx;
	}
	#avatarRight{
		float: right;
		margin-right: 30rpx;
	}
	.avatar{
		margin-top: 10rpx;
	}
	.icon{
		color: #bfbfbf;
		margin-left: 15rpx;
	}
	.otherAll{
		height: 100rpx;
		margin-left: 30rpx;
		border-bottom: 1rpx solid #bfbfbf;
		font-size: 30rpx;
	}
	.name{
		float: left;
		color: #000;
		height: 100rpx;
		line-height: 100rpx;
	}
	.right{
		float: right;
		display: flex;
		color: #707070;
		height: 100rpx;
		line-height: 100rpx; 
		margin-right: 30rpx;
	}
</style>